@import url(../fonts/Lato/Lato.css);
@import url(../../app_components/fit-img/fit-img.css);
@import url(../../bower_components/slick-carousel/slick/slick.css);
.svg-symbols {
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

[class^='icon-svg-'] *, [class*=' icon-svg-'] * {
  pointer-events: none;
}

.icon-svg-angle-right {
  width: 0.61156em;
  height: 1em;
}

.icon-svg-expand-more {
  width: 1.5em;
  height: 1em;
}

.icon-svg-place {
  width: 0.75em;
  height: 1em;
}

.icon-svg-search {
  width: 1em;
  height: 1em;
}

.icon-svg-visibility {
  width: 1.46588em;
  height: 1em;
}

:focus {
  outline: 0;
}

.container-fluid {
  max-width: 1360px;
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  color: #141414;
  background-color: #efefef;
  padding-top: 1.6875rem;
  padding-bottom: 2.5rem;
}

.header__tel {
  color: #404040;
  font-size: 0.8125rem;
  font-weight: 400;
  line-height: 1.2;
}

.header__tel-lnk {
  display: inline-block;
  vertical-align: top;
  color: #141414;
  text-decoration: none;
}

.header__tel-lnk:hover, .header__tel-lnk:focus {
  color: #141414;
  text-decoration: none;
}

.header__col-2 {
  text-align: center;
}

.header__logo {
  width: 221px;
  vertical-align: top;
}

.logo {
  display: inline-block;
}

.logo__img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.root {
  min-width: 320px;
}

.root ::selection {
  color: white;
  background: #007bff;
}

.root__body {
  display: flex;
  flex-flow: column;
}

.root__body-layout {
  display: flex;
  flex-flow: column;
  overflow: hidden;
  min-height: 100vh;
}

.root__header {
  flex: none;
}

.root__main {
  flex: none;
}

.root__footer {
  flex: none;
  margin-top: auto;
}
